import NavigationBar from '@/components/NavigationBar';
import { onMounted, reactive, ref } from 'vue';
import Sortable from 'sortablejs';
export default {
  name: 'AllMatterEditorController',
  components: {
    NavigationBar
  },

  setup() {
    const itemList = ref(null);
    const isEditor = ref(false);
    let myMatters = reactive([{
      title: '订单审批',
      value: 0
    }, {
      title: '售后审批',
      value: 1
    }, {
      title: '评论审核',
      value: 2
    }, {
      title: '工程机管理',
      value: 3
    }]);
    const noMatters = reactive([{
      title: '卡券管理',
      value: 4
    }, {
      title: '商品管理',
      value: 5
    }, {
      title: '库存管理',
      value: 6
    }, {
      title: '政策管理',
      value: 7
    }]);

    const deleteEvent = index => {
      const item = myMatters[index];
      myMatters.splice(index, 1);
      noMatters.push(item);
    };

    const addEvent = index => {
      const item = noMatters[index];
      noMatters.splice(index, 1);
      myMatters.push(item);
    };

    onMounted(() => {
      Sortable.create(itemList.value, {
        sort: true,
        animation: 150,
        onStart: function (evt) {
          evt.item.style.backgroundColor = 'white';
          evt.item.style.color = 'white';
        },
        onEnd: function (evt) {
          evt.item.style.backgroundColor = '#F1F1F1';
          evt.item.style.color = '#000000';
          const array = [...myMatters];
          const oldItem = myMatters[evt.oldIndex];
          const newItem = myMatters[evt.newIndex];
          array[evt.oldIndex] = newItem;
          array[evt.newIndex] = oldItem;
          myMatters = array;
        }
      });
    });
    return {
      myMatters,
      noMatters,
      isEditor,
      deleteEvent,
      addEvent,
      itemList
    };
  }

};